
import { ITask } from "@/types/task";
import { defineComponent } from "vue";

export default defineComponent({
  data: () => ({
    title: "",
    task: "",
  }),
  methods: {
    addTask() {
      if (this.title && this.task) {
        let newTask: ITask = {
          id: Date.now(),
          title: this.title,
          task: this.task,
        };
        this.$emit("addTask", newTask);
        this.title = "";
        this.task = "";
      }
    },
  },
});
